.contact-form{
    display: flex;
    height: 90vh;
    padding: 0 3rem 0 3rem;
    margin-top:0.5rem;
}
.c-right{
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
}
.c-right>form{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
}
.c-right .user{
    border: 2px solid var(--orange);
    outline: none;
    padding: 0.3rem;
    font-family: sans-serif;
    font-size: 16px;
    border-radius: 8px;
    width:20rem;
    height:2rem;
}
textarea{
    height: 4rem!important;
    width: 399px !important;
}
.c-blur1{
    top:1rem;
    left:8rem;
}
