.services{
    display: flex;
    height: 90vh;
    padding: 0 3rem 0 3rem;
    margin-bottom: 13rem;
    margin-top:0.5rem;
}
.awesome{
    display:flex;
    flex-direction: column;
    position: relative;
    gap:0.5rem;
}
.awesome>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 2.5rem;
}
.awesome>:nth-child(2){
    color: var(--orange);
    font-weight: bold;
    font-size: 2.5rem;
}
.awesome>:nth-child(3){
    margin-top: 1rem;
    font-size: 14px;
    color: var(--grey);
}
.s-button{
    width:8rem;
    height:2.5rem;
    margin-top:4rem;
}
.cards{
    position:relative;

}
.cards>*{
    position:absolute;
}
.s-blur2{
    left: 14rem;
    top: 8rem;
}
.s-blur1{
    top: 13rem;
    left: -18rem;
}