.card{
    height:13rem;
    width:10rem;
    position:absolute;
    display:flex;
    flex-direction: column;
    gap:1rem;
    align-items:center;
    text-align: center;
    background:rgba(255,255,255,0.26);
    border: 7px solid var(--orangeCard);
    box-shadow: var(--orangeCard);
    border-radius: 26px;
    padding: 0px 26px 2rem 26px;
}
.card :nth-child(1)
{
    transform: scale(0.6);
    margin-bottom: -2rem;
}
.card :nth-child(3){
    color: var(--gray);
    font-size:16px;
}
.card :nth-child(4){
    background: #ffffff;
    box-shadow: 0px 19px 60px rgba(0,0,0,0.08);
    border-radius: 7px;
    border: none;
    color: #5290fd;
    
}