.works{
    padding: 0 3rem 0 3rem;
    display: flex;
    flex-direction: row;
    height:90vh;
    margin-top: 4rem;
}
.w-right{
    position:relative;
}
.w-mainCircle{
    left:9rem;
    position: relative;
    width:18rem;
    height:18rem;
    border-radius: 100%;
    box-shadow: var( --smboxShadow);
    background: white;
    align-items: center;
    justify-content: center;
    display: flex;
}

.w-mainCircle>:nth-child(1)
{
    left:-2%;
    bottom:-2%;
}
.w-mainCircle>:nth-child(2)
{
    right:-2%;
    bottom:-2%;
}
.w-mainCircle>:nth-child(3)
{
    right:-2%;
    top:-2%;
}
.w-mainCircle>:nth-child(4)
{
    left:-2%;
    top:-2%;
}
.w-mainCircle>:nth-child(5)
{
    right:-20%;
}
.w-mainCircle>:nth-child(6)
{
    left:-20%;
}
.w-mainCircle>:nth-child(7)
{
    bottom:-20%;
}
.w-mainCircle>:nth-child(8)
{

}
.w-mainCircle>:nth-child(9)
{
    top:-20%;
}
.w-secCircle{
    width:6rem;
    height:6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    background: white;
    display: flex; 
    justify-content: center;
    align-items: center;
}
.w-backCircle{
    position:absolute;
    height:13rem;
    width:13rem;
    border-radius: 100%;
    z-index:-1;
}
.blueCircle{
    background:#1949b8 ;
    left:18rem;
    top: 0rem;
}
.yellowCircle{
    background:#f5c32c;
    left: 18rem;
    top: 8rem;
}